import React from 'react'
import { useSelector } from 'react-redux';
import Button from '../button/button'
import './cartItem.scss'

function CartItem({ img, title, totalCount, totalPrice, priceForM, area, lot, clicked, id, plusItem, minusItem }) {
 
  const  handlePlusItem = () => {
    plusItem(id);
}
const  handleMinusItem = () => {
  minusItem(id);
}
  return (
    <div className="cart-container">
      <div className="cart-image">
        <img className="apart-image" src={img} alt="Apartament" />
      </div>
      <div className="cart-text">
        <h4 className="cart-title">{title}</h4>
        <p> лот №{lot}</p>
        <div className="cart-addinfo">
          <p className="cart-lot"> <span className="data">{priceForM}</span> за м<sup>2</sup></p>
          <p> Площадь <span className="data">{area}</span> м<sup>2</sup> </p>
        </div>
      </div>
      <div className="cart-price">
        <div className="total-price">Стоимость товара: <b>{totalPrice}</b> ₽</div>
        <div className="cart__item-count">
          <Button className="  button--circle cart__item-count-minus">
            <svg
              onClick={handleMinusItem}
              width="10"
              height="10"
              viewBox="0 0 10 10"
              fill="none"
              xmlns="http://www.w3.org/2000/svg">
              <path
                d="M5.75998 5.92001L3.83998 5.92001L0.959977 5.92001C0.429817 5.92001 -2.29533e-05 5.49017 -2.29301e-05 4.96001C-2.2907e-05 4.42985 0.429817 4.00001 0.959977 4.00001L3.83998 4L5.75998 4.00001L8.63998 4.00001C9.17014 4.00001 9.59998 4.42985 9.59998 4.96001C9.59998 5.49017 9.17014 5.92001 8.63998 5.92001L5.75998 5.92001Z"
                fill="#EB5A1E"
              />
            </svg>
          </Button>
          <b>{totalCount}</b>
          <Button className=" button--circle cart__item-count-plus">
            <svg
              onClick={handlePlusItem}
              width="10"
              height="10"
              viewBox="0 0 10 10"
              fill="none"
              xmlns="http://www.w3.org/2000/svg">
              <path
                d="M5.92001 3.84V5.76V8.64C5.92001 9.17016 5.49017 9.6 4.96001 9.6C4.42985 9.6 4.00001 9.17016 4.00001 8.64L4 5.76L4.00001 3.84V0.96C4.00001 0.42984 4.42985 0 4.96001 0C5.49017 0 5.92001 0.42984 5.92001 0.96V3.84Z"
                fill="#EB5A1E"
              />
              <path
                d="M5.75998 5.92001L3.83998 5.92001L0.959977 5.92001C0.429817 5.92001 -2.29533e-05 5.49017 -2.29301e-05 4.96001C-2.2907e-05 4.42985 0.429817 4.00001 0.959977 4.00001L3.83998 4L5.75998 4.00001L8.63998 4.00001C9.17014 4.00001 9.59998 4.42985 9.59998 4.96001C9.59998 5.49017 9.17014 5.92001 8.63998 5.92001L5.75998 5.92001Z"
                fill="#EB5A1E"
              />
            </svg>
          </Button>
        </div>

      </div>
      <div onClick={() => clicked(id)} className="cart-remove">
        <div className="cart-remove__text">Удалить позицию</div>
        <Button className="button--circle">
          <svg
            width="14"
            height="14"
            viewBox="0 0 10 10"
            fill="none"
            xmlns="http://www.w3.org/2000/svg">
            <path
              d="M5.92001 3.84V5.76V8.64C5.92001 9.17016 5.49017 9.6 4.96001 9.6C4.42985 9.6 4.00001 9.17016 4.00001 8.64L4 5.76L4.00001 3.84V0.96C4.00001 0.42984 4.42985 0 4.96001 0C5.49017 0 5.92001 0.42984 5.92001 0.96V3.84Z"
              fill="#EB5A1E"
            />
            <path
              d="M5.75998 5.92001L3.83998 5.92001L0.959977 5.92001C0.429817 5.92001 -2.29533e-05 5.49017 -2.29301e-05 4.96001C-2.2907e-05 4.42985 0.429817 4.00001 0.959977 4.00001L3.83998 4L5.75998 4.00001L8.63998 4.00001C9.17014 4.00001 9.59998 4.42985 9.59998 4.96001C9.59998 5.49017 9.17014 5.92001 8.63998 5.92001L5.75998 5.92001Z"
              fill="#EB5A1E"
            />
          </svg>
        </Button>
      </div>

    </div>
  )
}

export default CartItem
